import { Directive, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { EagleInfoClientError } from '@main-shared-lib';

@Directive({})
export class FormButtonBase
implements OnInit {
  @Input({ required: true }) posting!: boolean;
  @Input({ required: true }) disabled!: boolean;
  @Input({ required: true }) tooltip!: string;
  @Input({ required: true }) btnText = 'Save';
  @Output() save: EventEmitter<void> = new EventEmitter<void>();

  ngOnInit(): void {
    if (!this.save.observers.length) {
      throw new EagleInfoClientError('The "submit" event must be handled by the parent component.');
    }
  }
}
